  display: flex;
  padding: 80px;
}

.homepageContent h1 h3{
  color: white;
}
#homepage  h3{
    color: white;
  }
  
.artistDiv {
  display: flex;
  border: 1px solid black;
  flex-direction: column;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 5px;
  border-radius: 10px;
  
  width: 200px;
  box-shadow: 1px 1px 5px 0 #d2d2d2;
  text-align: center;
  margin: 15px;
  height: 130px;
}



.artistDiv:hover {
    box-shadow: 1px 10px 26px 1px #d2d2d2!important;
    -webkit-box-shadow: 0 10px 26px 1px #d2d2d2!important;
    border-radius: 10px;
    margin-bottom: 1vh;
    transition: box-shadow .5s ease-in-out;
    transition: height .3s ease-in-out;
    height: 150px;
}